@import url(https://fonts.googleapis.com/css?family=Montserrat|Oswald);
.navbar{background:#ebebeb;font-size:1.2rem;font-family:"Oswald", sans-serif}.navbar .logout-btn{border:none;background:transparent;font-family:"Oswald", sans-serif}

.br-posts{position:relative;background:#fff;border-radius:5px;height:170px;margin:15px auto;border:1px solid rgba(0,0,0,0.125);padding:0 20px;width:100%}.br-posts .br-posts__post-title{font-size:1.4rem;font-family:"Oswald", sans-serif;color:#000;margin-top:20px;overflow:hidden;text-overflow:ellipsis;padding-bottom:5px}.br-posts .br-posts__post-content{margin-top:10px;margin-bottom:10px;width:100%}.br-posts .br-posts__post-info{position:absolute;bottom:15px;left:20px;width:100%;text-align:center}.post-view-post .post-detail p{margin-top:20px}.post-view-post .post-title{margin-top:20px;margin-bottom:15px}.row .post-title{width:100%}.post-view-post .post-creation-time,.post-view-post .post-creator{font-size:1rem;margin-left:10px;font-family:"Oswald", sans-serif}.comments{margin-top:40px}.comments .comment{font-size:1rem;padding:15px;border-bottom:1px solid rgba(0,0,0,0.085)}.comments .comment-user{font-weight:bold;display:block}.comments .comment-user span{font-weight:normal;font-size:0.8rem;margin-left:5px;padding-right:10px}

.Toastify .Toastify__toast-container .error{background:#d93f2db8;color:#fff}.Toastify .Toastify__toast-container .success{background:#4dbd74;color:#fff}

.br-post-detail p{margin-top:20px}.br-post-title{margin-top:20px;margin-bottom:15px;word-break:break-word;width:100%}.br-post-creation-time,.br-post-creator{font-size:1rem;margin-left:10px;font-family:"Oswald", sans-serif}

.br-comment{font-size:1rem;padding:15px;margin-top:40px;border-bottom:1px solid rgba(0,0,0,0.085)}.br-comment .br-comment__comment-user{font-weight:bold;display:block}.br-comment .br-comment__comment-user span{font-weight:normal;font-size:0.8rem;margin-left:5px;padding-right:10px}

.ant-form-item-control-input-content textarea{width:100%}

body{background:#f7f7f7;margin:0;padding:0;font-size:1rem;line-height:1.5rem;letter-spacing:0.2px}body h3,body h2{font-family:"Oswald", sans-serif}body p{font-family:"Montserrat", sans-serif;word-break:break-word;max-width:100%}body button{cursor:pointer}body i{margin-right:8px;padding-left:10px}.container{margin-top:50px}.br-post-view__post-view-comment,.br-post-view__post-view-post,.post-form,.user-form{margin-top:50px;background:#fff;padding:30px;border-radius:5px;box-shadow:1px 1px 5px 0px #a3a3a3}.br-error{color:red}.ant-form-item{display:block}

